[ripple] {
  cursor: pointer;
  overflow: hidden;
  position: relative
}
.ripple {
  display: block;
  position: absolute;
  background: #424242;
  border-radius: 100%;
  transform: scale(0)
}
.ripple.animate {
  animation: ripple .65s linear
}
@keyframes ripple {
